import React, {Component} from "react";

import Utils from '../../utils/Utils';

const utils = new Utils();

const space = {
    marginLeft: "8px"
}

export default function Admin(props){
    return  <div className="col-md-6 col-sm-6">
                <div className="row">
                    <div className="col-md-7 col-sm-7">
                        <h4>{props.admin.username}</h4>
                    </div>
                    <div className="col-md-7 col-sm-7">
                        <h4>{props.admin.firstname} {props.admin.lastname}</h4>
                        <h5>{utils.getDate(props.admin.created)}</h5>
                    </div>
                    <div className="col-md-5 col-sm-5">
                        <h5>{props.admin.phone_number}</h5>
                        <h4>{props.admin.priviledge}</h4>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-offset-6 col-md-6 col-sm-offset-6 col-sm-6">
                        <button onClick={props.deleteClicked(props.admin)} className="laserbtn laset-red-bg laser-inline">delete</button>
                        <button style={space} onClick={props.editClicked(props.admin)} className="laserbtn laset-blue-bg laser-inline">edit</button>
                    </div>
                </div>
            </div>
} 